@import "helpers/config";
@import "helpers/container";
@import "helpers/layout-flex";
@import "helpers/font";
@import "helpers/status";
@import "helpers/button-hover";

body {
    background: #f6f6f6;
    @extend %sans; }

h1 {
    font-size: 34px;
    font-weight: 700;
    color: #616161; }

p {
    font-size: 26px; }


.header {
    background: #ffa409;
    @include flex-vert-center;
    justify-content: space-between;
    @include boxing(112px, 750px);
    .header-left {
        flex: 2;
        @include flex-hor-left;
        span {
            display: inline-block;
            font-size: 38px;
            color: #fff; } }
    .header-right {
        flex: 1;
        @include flex-hor-right; }
    .btn-share, .btn-like, .btn-back {
        @include image(32px,30px); } }

.product-img {
  @include boxing(710px, 750px);
  img {
    @include boxing(856px, 750px, null, (top: fluid-size(-146px)) );
    position: relative;
    z-index: -1; } }

.product-info {
  @include flex-vert-center;
  @include boxing(120px, 750px, $black );
  color: #fff;
  .product-name, .product-price {
    flex: 1;
    font-weight: 300; }
  .product-name {
    @include flex-hor-left;
    margin-left: fluid-size(40px);
    font-size: fluid-size(28px); }
  .product-price {
    @include flex-hor-right;
    margin-right: fluid-size(40px);
    font-size: fluid-size(30px); } }

.product-option {
  @include flex-vert-center;
  @include boxing(238px, 750px, $white-bg);
  div {
    font-size: fluid-size(50px);
    line-height: fluid-size(55px);
    color: $sand; }
  p {
    font-size: fluid-size(26px);
    font-style: italic;
    font-weight: 300;
    text-transform: capitalize;
    color: $grey-unit; }
  .size, .quantity, .color {
    @include flex-vert-center;
    flex: 1;
    flex-flow: column;
    .btn-color {
       width: fluid-size(36px);
       height: fluid-size(36px);
       border: fluid-size(7px) solid $grey-bg;
       border-radius: 50%;
       background: $black; } }
  .color div {
    line-height: fluid-size(55px); } }

.add-basket {
  @include flex-center;
  @include boxing(120px, 750px, $sand );
  font-size: fluid-size(24px);
  color: #fff;
  @include btn-hover($sand); }
