@mixin flex-box {
  display: flex; }

@mixin flex-center {
  @include flex-box;
  align-items: center;
  justify-content: center; }

@mixin flex-vert-center {
  @include flex-box;
  align-items: center; }

@mixin flex-hor-center {
  @include flex-box;
  justify-content: center; }

@mixin flex-hor-left {
  @include flex-box;
  justify-content: flex-start; }

@mixin flex-hor-right {
  @include flex-box;
  justify-content: flex-end; }
